@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* base styles */
body {
  font-family: Raleway, sans-serif;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

@media screen and (max-width: 667px) {
  body {
    font-size: 1em;
  }  
}
.App {
  max-width: 860px;
  margin: 40px auto;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background: #c23866;
  color: #fff;
}

.cards {
  margin: 25px 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  position: relative;
}

.cards img {
  max-width: 100%;
  height: auto;
  border: 2px solid white;
  border-radius: 10px;
}

@media screen and (max-width: 896px) {
  .App {
    max-width: 600px;
    margin: 20px auto;
  }

  h1 {
    font-size: 1.25em;
  }

  button {
    margin-top: 15px;
  }

  button:hover {
    background: transparent;
    color: #fff;
  }

  .cards {
    margin: 20px 0;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 10px;
    position: relative;
  }
}
.front {
  position: absolute;
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition: all ease-in 0.2s;
}

.flipped .front {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}

.flipped .back {
  -webkit-transform: rotateY(90deg);
          transform: rotateY(90deg);
  transition-delay: 0s;
}

@media screen and (max-width: 896px) {
  .front {
    position: absolute;
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    width: 90px;
    height: 90px;
  }
}
