.App {
  max-width: 860px;
  margin: 40px auto;
}

button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background: #c23866;
  color: #fff;
}

.cards {
  margin: 25px 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  position: relative;
}

.cards img {
  max-width: 100%;
  height: auto;
  border: 2px solid white;
  border-radius: 10px;
}

@media screen and (max-width: 896px) {
  .App {
    max-width: 600px;
    margin: 20px auto;
  }

  h1 {
    font-size: 1.25em;
  }

  button {
    margin-top: 15px;
  }

  button:hover {
    background: transparent;
    color: #fff;
  }

  .cards {
    margin: 20px 0;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 10px;
    position: relative;
  }
}